@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  src: url(https://static.contineljs.com/fonts/Roboto-Light.woff2) format("woff2"), url(https://static.contineljs.com/fonts/Roboto-Light.woff) format("woff");
}
@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  src: url(https://static.contineljs.com/fonts/Roboto-Regular.woff2) format("woff2"), url(https://static.contineljs.com/fonts/Roboto-Regular.woff) format("woff");
}
@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  src: url(https://static.contineljs.com/fonts/Roboto-Medium.woff2?v=2) format("woff2"), url(https://static.contineljs.com/fonts/Roboto-Medium.woff) format("woff");
}
@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  src: url(https://static.contineljs.com/fonts/Roboto-Bold.woff2) format("woff2"), url(https://static.contineljs.com/fonts/Roboto-Bold.woff) format("woff");
}
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  box-sizing: border-box;
  background-color: #E5E5E5;
}

.box__container {
  background-color: white;
  width: 660px;
  height: 330px;
  margin: 0 auto;
  box-sizing: border-box;
  border: 1px solid #A5A5A5;
}

.box__brand {
  display: flex;
  margin: 15px;
}
.box__brand--left {
  flex: 1;
  display: flex;
  align-items: flex-end;
}
.box__brand--left .brand__left--img {
  width: 35px;
  height: 35px;
}
.box__brand--left .brand__left--title {
  width: 160px;
  height: 21px;
}
.box__brand--left .brand__left--title p {
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  color: #236C62;
}
.box__brand--right {
  flex: 1;
}
.box__brand--right .brand__right--logo {
  float: right;
  width: 120px;
  height: 26px;
  background: radial-gradient(143.96% 215.88% at 45.83% 54.55%, rgba(11, 24, 27, 0.5) 0%, rgba(24, 121, 104, 0.5) 51.28%, rgba(0, 200, 164, 0.5) 100%), #20C8A7;
  border-radius: 10px;
  line-height: 26px;
  text-align: center;
}
.box__brand--right p {
  color: #FFFFFF;
  font-style: normal;
  font-weight: 700;
  font-size: 11px;
}
.box__content--rec {
  position: absolute;
  z-index: 0;
  width: 660px;
  height: 133px;
  margin-top: 50px;
  background: radial-gradient(143.96% 215.88% at 45.83% 54.55%, rgba(11, 24, 27, 0.5) 0%, rgba(24, 121, 104, 0.5) 51.28%, rgba(0, 200, 164, 0.5) 100%), #20C8A7;
}
.box__content--bullet {
  z-index: 99;
  float: right;
  margin-right: 150px;
}
.box__content--bullet .content__bullet {
  width: 6px;
  height: 6px;
  display: inline-block;
  border-radius: 100%;
  background: black;
  opacity: 0.2;
}
.box__notice {
  margin-left: 15px;
  width: 276px;
  height: 24px;
  font-style: italic;
  font-size: 10px;
  line-height: 12px;
  color: #656565;
}

.box__center {
  display: flex;
}
.box__center--video {
  z-index: 99;
  background: #E5E5E5;
  box-shadow: 0px 4px 4px rgba(185, 185, 185, 0.25);
  width: 304px;
  height: 171px;
  margin: 15px;
}
.box__center--card {
  z-index: 99;
  right: 0px;
  margin: 15px;
  width: 300px;
  height: 205px;
  background: #FFFFFF;
  box-shadow: 0px 0px 7px rgba(136, 136, 136, 0.25);
  border-radius: 10px;
  display: flex;
}
.box__center--card .card__left {
  flex: 1;
  margin: 15px;
}
.box__center--card .card__left--img {
  width: 123px;
  height: 123px;
  left: 15px;
  top: 15px;
}
.box__center--card .card__left--text {
  left: 5%;
  right: 54%;
  top: 72.2%;
  bottom: 9.76%;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 130%;
}
.box__center--card .card__right {
  flex: 1;
  margin: 15px;
}
.box__center--card .card__right--logo {
  width: 81.67px;
  height: 30px;
  top: 15px;
}
.box__center--card .card__right--content {
  top: 50px;
  width: 130px;
  height: 135px;
  font-weight: 300;
  font-size: 13px;
  line-height: 120%;
}